import React from 'react';
import { connect } from 'react-redux';
import { setSirene } from 'store/actions/sirene';
import LayoutPage from 'layout/LayoutPage/LayoutPage.js';
import { TextField, ButtonBase, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import classes from './PageCalcSound.module.scss';
import SelectSiren from './SelectSiren/SelectSiren';

const CssTextField = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#2196f3',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#2196f3',
        borderWidth: 2
      }
    }
  }
})(TextField);

const CssTextFieldReadOnly = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#888',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#888',
        borderWidth: 1
      },
      '&.Mui-focused fieldset': {
        borderColor: '#888',
        borderWidth: 1
      },
      '&.Mui-focused:hover fieldset': {
        borderColor: '#888',
        borderWidth: 1
      }
    }
  }
})(TextField);

const CssTextFieldResult = withStyles({
  root: {
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: '#008800',
      borderWidth: 2
    },
    '& .MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline': {
      borderColor: '#880000'
    }
  }
})(TextField);

class PageCalcSound extends React.Component {
  constructor(props) {
    super(props);

    const Nsh = 75;
    const Nsum = 0;
    const N0 = 112;
    const Pe = 50;
    const Nzv = 0;
    const L = 20;
    const R = 0;
    const Lmax = 0;
    const Nzvr = 0;

    const input = {
      Nsh,
      Nsum,
      N0,
      Pe,
      Nzv,
      L,
      R,
      Lmax,
      Nzvr
    };

    this.state = {
      input: this.calcSound(input),
      showSireneList: false
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.sirene !== this.props.sirene) {
      const { sirene } = this.props;

      if (
        !sirene ||
        typeof sirene !== 'object' ||
        (!sirene.sensitivity || !sirene.inputPower)
      ) {
        return;
      }

      const { input } = this.state;
      input.N0 = sirene.sensitivity;
      input.Pe = sirene.inputPower;

      this.setState({ input: this.calcSound(input) });
    }
  }

  //    x
  getBaseLog(x, y) {
    return Math.log(y) / Math.log(x);
  }
  //    10
  LOG(x) {
    return Math.log(x) / Math.log(10);
  }
  ROUND(x) {
    return Math.round(x * 100) / 100;
  }

  calcSound = inputData => {
    const input = { ...inputData };
    input.Nsum = input.Nsh + 15;
    input.Nzv = this.ROUND(input.N0 + 10 * this.LOG(input.Pe));
    input.R = this.ROUND(20 * this.LOG(input.L));
    input.Lmax = this.ROUND(Math.pow(10, (input.Nzv - input.Nsum) / 20));
    input.Nzvr = this.ROUND(input.Nzv - input.R);

    Object.entries(input).map(([key, value]) => (input[key] = String(value)));

    return input;
  };

  handleBtnTypeSirensClick = () => {
    this.setState(prevState => ({
      showSireneList: !prevState.showSireneList
    }));
  };

  handleChange = name => e => {
    const value = +e.target.value;

    switch (name) {
      case `Nsh`: {
        if (value < 0) return;
        break;
      }
      default:
        break;
    }

    const { input } = this.state;
    input[name] = value;

    this.setState({ input: this.calcSound(input) });
  };

  render() {
    const { input, showSireneList } = this.state;

    return (
      <LayoutPage>
        <Typography variant="h1" gutterBottom>
             
        </Typography>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            1.     
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="N"
                value={input.Nsh}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('Nsh')}
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                   
              </p>
              <p className={classes.stepRowTextP}>
                      
                  ,     12.1.036-81
                .       , 
                2.2.4/2.1.8.562-96    ,   ,
                        
                51.13330.2011 "  "
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            2.     
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="N"
                value={input.Nsum}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                    (  
                +15)
              </p>
              <p className={classes.stepRowTextP}>
                  . 4.2.  3.13130.2009  
                       15  
                      .
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            3.      
               
          </p>

          {showSireneList ? (
            <SelectSiren handleCancelBtnClick={this.handleBtnTypeSirensClick} />
          ) : (
            <div className={classes.setSirens}>
              <ButtonBase
                focusRipple
                className={classes.setSirensBtn}
                onClick={this.handleBtnTypeSirensClick}
              >
                <p>  </p>
              </ButtonBase>
            </div>
          )}

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="N0"
                value={input.N0}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('N0')}
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                 
              </p>
              <p className={classes.stepRowTextP}>
                     1  
                 1 ,  
              </p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="P"
                value={input.Pe}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('Pe')}
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                   
              </p>
              <p className={classes.stepRowTextP}> </p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="N"
                value={input.Nzv}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                    
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            4.       
                
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="L"
                value={input.L}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('L')}
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                     
              </p>
              <p className={classes.stepRowTextP}>
                  -      
                 ,  .
              </p>
            </div>
          </div>
          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="R"
                value={input.R}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                      
              </p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="L"
                value={input.Lmax}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                   
              </p>
              <p className={classes.stepRowTextP}>
                      " 
                  15        
                 " (3.13130.2009 .4.2)
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            5.       
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldResult
                className={classes.margin}
                label="N "
                error={input.Nzvr < input.Nsum}
                value={input.Nzvr}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}></p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                      
              </p>
            </div>
          </div>
        </div>

        {input.Nzvr >= input.Nsum ? (
          <div className={classes.stepGreen}>
            <p>  </p>
            <p>    3.13130.2009</p>
          </div>
        ) : (
          <div className={classes.stepRed}>
            <p>  </p>
            <p>    </p>
          </div>
        )}
      </LayoutPage>
    );
  }
}

const mapStateToProps = state => ({
  sirene: state.sirene.sirene
});

const mapDispatchToProps = {
  setSirene
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageCalcSound);
