const LCDSymbolsCalc = {};
for (let key in LCDSymbols) {
  const result = LCDSymbols[key].map(it => {
    if (it === 0x00) {
      return [`0`, `0`, `0`, `0`, `0`, `0`, `0`, `0`];
    }

    const res = it
      .toString(2)
      .split(``)
      .reverse();

    for (let i = res.length; i < 8; i++) {
      res.push(`0`);
    }

    return res;
  });

  LCDSymbolsCalc[key] = result;
}

export const LCDSymbolsArr = LCDSymbolsCalc;

