// added id for elements
// id = index in shema.elements
META_17820.elements.map((it, i) => {
    it.id = i;

    //   id
    //  breakPoint   
    if (it.type === `lines`) {
        it.lines.map((item, j) => {
            item.id= j;

            if (item.breakPoint){
                item.breakPoint.x = Math.min(item.x1, item.x2) + (item.x2 - item.x1)/2 - BREAKPOINT_DIF_X;
                item.breakPoint.y = Math.min(item.y1, item.y2) + (item.y2 - item.y1)/2 - BREAKPOINT_DIF_Y;
            }

            return item
        })
    }

    return it
});

// eslint-disable-next-line
for (let key in META_17820.message) {
    META_17820.message[key].id = `message_${key}`;
}

export default META_17820;
